
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { fetchListByPN } from '@/api/mat'
import { showAlertAuto } from '@/utils/index'
export default {
data () {
return {
items: [],
total: 0,
partNo: '',
amecoItems: []
}
},
components: {
XHeader,
Search,
Group,
Cell
},
methods: {
getList (val) {
this.$store.commit('UPDATE_LOADING_STATUS', true)
fetchListByPN({matpn: this.partNo}).then(response => {
this.$store.commit('UPDATE_LOADING_STATUS', false)
if (response.data.result === 'fail') {
showAlertAuto('提示！', response.data.info)
} else {
this.items = response.data.data
this.amecoItems = response.data.amecodata
}
})
},
onSubmit () {
this.$refs.matSearch.setBlur()
this.getList()
},
onCancel () {
this.partNo = ''
this.items = []
this.amecoItems = []
}
}
}



